$(document).ready(function(){
  $('#slider').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: true,
    prevArrow: '<img class="slider-leftarrow" src="../img/arl.png">',    
    nextArrow: '<img class="slider-rightarrow" src="../img/arr.png">',
    dotsClass: 'slider-dots',
    });
});

$(document).ready(function(){
  $('#slider-team').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    initialSlide: 5,
    dots: false,
    prevArrow: '<img class="our-team-wraper__left" src="./img/arl1.png">',    
    nextArrow: '<img class="our-team-wraper__right" src="./img/arr1.png">', 
    responsive: [{
            breakpoint: 1330,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
                dots: false
            }
        },
        {
            breakpoint: 990,
            settings: {
                variableWidth: false,
                slidesToShow: 1,
                slidesToScroll: 1,
               
                arrows: false,

            }
        }
    ]
    });
});

